html{
  scroll-behavior: smooth;
}

@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@700&family=Saira+Semi+Condensed&display=swap');

body{
  background-color: #ccc;
  transition: all 0.4s;
}

#menuFixo{
  position:fixed;
  width:100%;
  top:0px;
  display: flex;
  justify-content:space-around;
  align-items: center;
  background-color:white;
  border:1px solid #aaa;
  font-size: 15px;
  z-index: 0;
  font-family: 'Rajdhani';
  font-weight: 900;
  max-height: 300px;
  overflow: hidden;
  transition:all 0.4s;
}

.menuFixo_agoraOculto{
  max-height: 0px !important;
}

#menuFixo img{
  width:160px;
  height: 115px;
}


#menuFixo nav ul{
  list-style-type: none;
  transition: all 0.4s;
  margin:0;
  font-family: 'Rajdhani';
}

.li{
  display: inline;
  margin: 0 30px 0 30px;
  font-family: 'Rajdhani';
  font-size: 19px;
  font-weight: 900;
  padding:15px;
}

.li:hover {
  background-color: #ccc;
  cursor:pointer;
  padding:47px 15px 46px 15px;
}

.liAtivo{
  display: inline;
  margin: 0 30px 0 30px;
  font-family:'Rajdhani';
  font-size: 19px;
  background-color: #ccc;
  cursor:pointer;
  padding:44.8px 15px 44.5px 15px;
}

#menuFixo a{
  text-decoration: none;
  color:none;
}

.overflowHidden{
  display: inline-flex;
  overflow: hidden;
  margin:0;
  padding: 0;
  width: 215px;
}

.menuOculto{
  position: fixed;
  width: 142px;
  background-color: #bbb;
  z-index: 2;
  font-family: 'Rajdhani';
  transition: all 0.2s;
  overflow: hidden;
  box-shadow: 2px 2px 2px black;
}

.menuOculto_Posicionamento1{
  top:18.5%;
  left:17.9%;
}

.menuOculto_Posicionamento2{
  top:18.5%;
  left:33.8%;
}

.menuOculto nav ul{
  justify-content: center;
  text-align:center;
  padding:0;
  margin:0;
}

.menuOculto nav ul li{
  list-style-type: none;
  padding: 10px;
}

.menuOculto nav ul li:hover{
  background-color: #aaa;
  cursor:pointer;
}

#redes{
  display: inline;
  max-width: fit-content;
}

#redes img{
  width:35px;
  height:35px;
  margin:10px;
}

#botaoContato{
  padding:5px;
  margin-left: 20px;
  background-color: rgb(250,30,30);
  color:#fff;
  font-weight: 500;
}

#botaoContato:hover{
  cursor:pointer;
}

#divDeImg{
  position:static;
  width: 1200px;
  height:550px;
  margin: 130px 0 0 75px;
  border:1px solid cadetblue;
  background-image: url('components/divDeImg/imgs/imgOfice.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.formulario{
  display: block;
  position: fixed;
  left:35%;
  top:4%;
  justify-content:space-evenly;
  text-align:center;
  width:400px;
  height:fit-content;
  max-height:fit-content !important;
  background-color:cornflowerblue;
  border-radius:5%;
  box-shadow: 2px 2px 2px black;
  padding:25px;
  z-index:2;
  transition:all 0.3s;
}

.formulario label{
  font-family: 'Rajdhani';
  margin:0 180px 0 0;
  
}

input{
  width:60%;
  margin-bottom:10px;
}

.labelTA{
  font-family:'Rajdhani';
  margin: 0 230px 80px 0 !important;
}

textarea{
  width:80%;
}

.formulario button{
  width:30%;
  height: 8%;
  background-color:mediumseagreen;
  color:#fff;
  font-family: 'Rajdhani';
  border-radius: 5%;
}

.divTexto{
  position: static;
  top:100px;
  margin:75px;
  font-family: 'Saira Semi Condensed';
  padding:30px;
}

#divVideo{
  display: flex;
  justify-content: center;
  width:100%;
  margin:30px 0 30px 0;
  padding: 30px;
}

#divFooter{
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items:flex-start;
  margin-top:50px;
  background-color: #25a;
  color:white;
}

.footerChild{
  display: flex;
  flex-direction: column;
  justify-content:left;
  margin: 30px;
}

.footerChild nav ul li{
  list-style-type: none;
}

.showOverlay{
  display:block !important;
  max-height: 2000px;
}

.show{
  display:block !important;
  max-height: 200px;
}

.hide{
  /* display: none !important; */
  overflow: hidden;
  max-height: 0px;
}

.overlay{
  position: fixed;
  top:0;
  width: 100%;
  height:100%;
  background-color: blue;
  opacity:0;
  z-index: 1;
}

.blur{
  filter:blur(1px);
  background-color: white;
  opacity:0.6;
}

.formShow{
  opacity:1;
}

.formHide{
  opacity:0;
  z-index: -1;
}